import React,{Component} from 'react';
import axios from 'axios';
import {Consumer } from '../../context';
class Search extends Component{


    state={
        title:''
    }

    OnTrackChange=(e)=>{
        this.setState({title:e.target.value});
    }

    findTrack=(dispatch,e)=>{
        e.preventDefault();
        axios.get(`https://cors-anywhere.herokuapp.com/http://api.musixmatch.com/ws/1.1/track.search?q_track=${this.state.title}&page_size=10&page=1&s_track_rating=desc&apikey=${process.env.REACT_APP_MM_KEY}`).then(res=>{
  
            dispatch({
                type:'SEARCH_TRACKS',
                tracks: res.data.message.body.track_list
            });
            this.setState({title:''});
})
.catch(err=>console.log(err)); 
    }

    render(){
        return (<Consumer>
            
            {(value)=>{

                const {dispatch} =value;
                return (<div className=".card card-body mb-4 p-4">
                <h1 className="display-4 text-center">
                <i className="fas fa-music"></i> Search for a song 
                </h1>
                <p className="lead text-center">Get the lyrics for any song </p>
                <form onSubmit={this.findTrack.bind(this,dispatch)} >
                <div className="form-group">
                <input type="text" name="title"  className="form-control form-control-lg" placeholder="song title" value={this.state.title} onChange={this.OnTrackChange} />
                <button className="btn btn-primary btn-lg btn-block mb-5" type="submit">Get Track Lyrics</button>
                </div>
                </form>
                </div>)
            }}
            </Consumer>)
    }
}

export default Search;


